body {
  background-color: #9266ff;
}

.App{
  background-color: #3f3160;
  max-height: 500px;
  height: auto;
  width: 400px;
  margin: 40px auto;
  padding-bottom:40px;
  overflow-y: scroll;
}

#todo-form input{
  background-color: rgb(27,112,137);
  border: 0;
  width: 200px;
  height: 50px;
  padding: 0 20px;
  margin: 20px;
  font-size: 18px;
  border-radius: 20px;
  color: #fff;

}

#todo-form input::placeholder {
  color: rgba(255,255, 255,0.5)
}

#todo-form input:focus, #todo-form button:focus {
  outline: none;
}

#todo-form button{
  height:50px;
  width: 80px;
  border-radius:5px;
  border: 0;
  background-color: rgb(202, 156, 24);
  cursor: pointer;
  font-weight:bolder;
  color: #fff;
}
